import { Component, OnInit } from '@angular/core';

import {ChartDB} from './chart/chart-data';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';


@Component({
  selector: 'app-health-dashboard',
  templateUrl: './health-dashboard.component.html',
  styleUrls: ['./health-dashboard.component.scss']
})
export class HealthDashboardComponent implements OnInit {
  public powerCardChartData1: any;
  public powerCardChartData2: any;
  public powerCardChartData3: any;
  public powerCardChartData4: any;

  public chartDB: any;

  healthUrl: string = "https://app.netdata.cloud/spaces/easerverstats/rooms/alpharoom/nodes/0e3c2a24-faad-11ea-bbac-fa163e244b4a#menu_apps";
  urlSafe: SafeResourceUrl;

  serverId: number = 0;
  constructor(
    public sanitizer: DomSanitizer,
  ) { 
    this.powerCardChartData1 = ChartDB.powerCardChartData1;
    this.powerCardChartData2 = ChartDB.powerCardChartData2;
    this.powerCardChartData3 = ChartDB.powerCardChartData3;
    this.powerCardChartData4 = ChartDB.powerCardChartData4;
    this.chartDB = ChartDB;
  }

  ngOnInit(): void {
    this.urlSafe = this.sanitizer.bypassSecurityTrustResourceUrl(this.healthUrl);
  }
  viewServerDetails(serverId: number){
    this.serverId ? this.serverId = 0 : this.serverId = serverId;
    switch(this.serverId){
      case 1:
        this.powerCardChartData1 = ChartDB.powerCardChartData1;
        this.powerCardChartData2 = ChartDB.powerCardChartData2;
        this.powerCardChartData3 = ChartDB.powerCardChartData3;
        this.powerCardChartData4 = ChartDB.powerCardChartData4;
        break;
      case 2:
        this.powerCardChartData1 = ChartDB.powerCardChartData1;
        this.powerCardChartData2 = ChartDB.powerCardChartData2;
        this.powerCardChartData3 = ChartDB.powerCardChartData3;
        this.powerCardChartData4 = ChartDB.powerCardChartData4;
        break;
      case 3:
        this.powerCardChartData1 = ChartDB.powerCardChartData1;
        this.powerCardChartData2 = ChartDB.powerCardChartData2;
        this.powerCardChartData3 = ChartDB.powerCardChartData3;
        this.powerCardChartData4 = ChartDB.powerCardChartData4;
        break;
      case 4:
        this.powerCardChartData1 = ChartDB.powerCardChartData1;
        this.powerCardChartData2 = ChartDB.powerCardChartData2;
        this.powerCardChartData3 = ChartDB.powerCardChartData3;
        this.powerCardChartData4 = ChartDB.powerCardChartData4;
        break;
      default:
        this.serverId = 0;
    }
  }
}
